import React from 'react'
import { connect } from 'react-redux'
import { localeChange } from '../../actions/locale'
import { injectIntl } from 'react-intl';

class Menu extends React.Component {
  render () {
    return (
        <ul className='menu'>
          {this.props.langs.map((lang, i) => 
            <li key={i} onClick={() => this.props.dispatch(localeChange(lang))} 
                className={`menu--item-${lang === this.props.intl.locale ? 'selected' : ''}`}>
              {lang}
            </li>)} 
        </ul>
    );
  }
}

export default injectIntl(connect()(Menu));