import { es } from '../lang/es';
import { en } from '../lang/en';
import * as locale from '../actions/locale';

const initialState = {
  lang: en.lang,
  messages: en.messages
};

export default(state = initialState, action) => {
  switch (action.type) {
    case locale.LOCALE_CHANGE:
      switch (action.locale) {
        case 'en':
          return { lang: en.lang, messages: en.messages };
        default:
          return { lang: es.lang, messages: es.messages };
      }
    default:
      return state;
  }
};

export function getLocale (state) {
  if (state.lang) {
    return state.lang;
  }
}
